import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { ServiceStatus} from '../models/service-status.enum';
import { Credentials } from '../models/credentials';
import { Subscription } from 'rxjs';
import { Router } from '@angular/router';
import { LoginServiceService } from '../services/login-service.service';


@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage implements OnInit, OnDestroy {
  loginForm: FormGroup;
  service$: Subscription;

  constructor(private fb: FormBuilder, private _router: Router, private _service: LoginServiceService) {
    this.loginForm = fb.group({
      user: ['', Validators.required],
      pass: ['', Validators.required]
    });
  }
  ngOnInit() {
  }
  ngOnDestroy() {
    if (this.service$) {
      this.service$.unsubscribe();
    }
  }

  onSaveClick() {
    if (this.loginForm.valid) {
      const cred = new Credentials();
      const logValues = this.loginForm.value;
      cred.email = logValues.user;
      cred.pass = logValues.pass;
      this.service$ = this._service.asyncDo(cred).subscribe({
        next(status) {
          window.location.href = `${window.location.origin}/customers/home`;
        },
        error(msg) {console.log(`Error login: ${msg || ''}`); }
      });
    }
  }

  onCancelClick() {
    this.loginForm.patchValue({
      user: '',
      pass: ''
    });
  }
}
